.page__detaill {
  max-width: 1170px;
  margin: auto;
}
.page__detaill .header__left {
  width: max-content;
}
.detail {
  padding-top: 11px;
  display: grid;
  grid-template-columns: 670px 424px;
  column-gap: 76px;
}

@media screen and (max-width: 1171px) {
  .detail {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
}
.detail__title {
  font-family: "Bebas Neue Pro";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 43px;
  /* or 108% */
  text-transform: uppercase;
  color: var(--violet);
  word-break: break-word;
}

.detail__author {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 14px;
}
.detail__author .detaillEvenement__socialMedia {
  padding: 14px 0 0 0;
}

.author__left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  color: #4f4f4f;
}

.detail__description {
  padding-top: 40px;
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  color: var(--gray2);
}

.publication__interest {
  padding: 50px 0 88px 0;
}
.interest__detaill {
  padding-top: 26px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  row-gap: 42px;
}
.interest__detaill > div {
  display: flex;
  gap: 19px;
}

@media screen and (max-width: 1171px) {
  .detail__title {
    font-size: clamp(25px, 5vw, 40px);
    line-height: 1.2;
  }
}
